body .swal2-container.swal2-backdrop-show, .swal2-container.swal2-noanimation {
    background: rgba(var(--bs-body-bg-rgb), .7);
}
.swal2-container {

.swal2-icon {
    transform: scale(0.8) !important;
}
.swal2-icon .swal2-icon-content {
    font-size: 2.3em;
}
.swal2-image {
    max-width: 100%;
    margin: 3em auto 1em;
    width: 40px;
}

.swal2-title {
    padding: 0em 1em 0;
    font-size: 1.2em;
    color: var(--bs-gray-800);
}
.swal2-html-container{
    font-size: 14px;
    color: var(--bs-body-color);
}

.swal2-popup {
    width: 28em;
    padding: 0 0 2.5em;
    font-size: 14px;
    background-color:var(--bs-body-bg);
    box-shadow: 0px 0px 10px rgb(var(--bs-primary-rgb), 0.25);
}
.swal2-close {
    width: 1em;
    height: 1em;
    margin-top: 0.2em;
    margin-right: 0.2em;
    margin-bottom: -1.2em;
    border-radius: 5px;
    font-size: 2em;
}

.swal2-close:hover{
    color: var(--bs-gray-700);
}
.swal2-actions {
    margin: 1.4em auto 0;
}

.swal2-styled.swal2-confirm,
.swal2-styled.swal2-cancel {
    border-radius: 4px;
    font-size: calc(10px + 0.1vw + 0.1vh);
    padding: 8px 14px;
    min-width: 100px;
    line-height: 1.5;
    font-weight: 500;
    border: 1px solid transparent;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.swal2-styled.swal2-confirm{
    background-color: #313A53;
    border-color: #313A53;
    color: #fff;
}

.swal2-styled.swal2-cancel{
    background-color: #F4F5FA;
    border-color: #F4F5FA;
    color: #000;
}



.swal2-html-container {
    margin: 0.5em 1.6em 0.3em;
}

.swal2-styled.swal2-confirm:focus,
.swal2-styled.swal2-cancel:focus {
    box-shadow: none;
}

.swal2-icon.swal2-warning {
    border-color: var(--bs-warning);
    color: var(--bs-warning);
}
.swal2-icon.swal2-success {
    border-color: var(--bs-success);
    color: var(--bs-success);
}
.swal2-icon.swal2-error {
    border-color: var(--bs-danger);
    color: var(--bs-danger);
}
.swal2-icon.swal2-info {
    border-color: var(--bs-info);
    color: var(--bs-info);
}
.swal2-icon.swal2-question {
    border-color: var(--bs-dark);
    color: var(--bs-dark);
}
}

[data-theme=dark] .swal2-container {
.swal2-styled.swal2-cancel{
    background-color: #1e1e2d;
    border-color: #1e1e2d;
    color: #ffffff;
}
}